*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  margin: 0 auto;
  padding: 0;
  font-family: "PT Mono", monospace, sans-serif;
  background-color: #ffffff; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  clip: rect(1px, 1px, 1px, 1px);
  border: 0;
  overflow: hidden; }

@font-face {
  font-family: "PT Mono";
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff") format("woff"), url("../fonts/PTMono-Regular.woff2") format("woff2"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"); }

.link-mixin {
  text-decoration: none;
  color: #ffffff; }

.desktop-wrapper {
  padding: 0 calc(50% - 705px); }

.page-header-contacts-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.page-header {
  min-height: 878px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 35px 10px;
  background-image: url("../img/header-bg@1x.jpg");
  background-position: center 0; }

.page-header__logo {
  position: relative;
  margin-right: auto;
